import "../styles/main.css";
import App from "../components/App";
import Service from "../components/Service";



document.addEventListener("DOMContentLoaded", () => {


    function Generator({root, app}){

        this.render = () => {
            document.querySelector(root).insertAdjacentHTML("beforeend", app)
        }
    
    
    }
    
    const root = new Generator({
        root: "#app",
        app: App(),
    });

    root.render();


  

    /* 
    
        Service function init (menu tabs, inputs)
    
    */


    const service = new Service();
    service.inputs.innerHTML = service.initDefaultTab();
    service.menuControl.addEventListener("click", (event) => {
        service.inputs.innerHTML = service.initTabs(event.target);

        service.initInputs();
        service.copySignature();
    });

    service.initInputs();
    service.copySignature();


   
    
})






