import img from "../images/default-img.png";


function InputBranch(item, {phoneNumber, email, name, lastname, address, company, url, thumbnail}){

    if(item.id === "Mobile"){
                 
        phoneNumber.textContent = item.value;
    }
    else if(item.id === "Email"){
        email.textContent = item.value;
    }
    else if(item.id === "Name"){
        name.textContent = item.value;
    }
    else if(item.id === "LastName"){
        lastname.textContent = item.value;
    }
    else if(item.id === "Address"){
        address.textContent = item.value;
    }
    else if(item.id === "Company"){
        company.textContent = item.value;
    }
    else if(item.id === "UrlTitle"){
        let data = item.value.split(",");

        if(data.length >= 1){

            for(let i = 0; i < data.length; i++){
                url[i].textContent = data[i].trim();
            }
        }
    }
    else if(item.id === "Url"){
        let data = item.value.split(",");

        if(data.length >= 1){

            for(let i = 0; i < data.length; i++){
                url[i].href = data[i].trim();
            }
        }
    }
    else if(item.id === "Image"){
     
      
        thumbnail.src = item.value || img;
        
    }
}

export default InputBranch;